<template>
	<view>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span style="font-size: 32rpx;color: #79666B;display: flex;justify-content: center;"><b>Product
						Purchase</b></span>
			</div>
			<el-form :label-position="labelPosition" label-width="140px" :style="{marginLeft: fromleft}">
				<el-form-item label="电子邮件	:">
					{{email}}
				</el-form-item>
				<el-form-item label="联系电话	:">
					{{phone}}
				</el-form-item>
			</el-form>
			<el-table :data="tableData" style="width: 100%" sum-text="总计" show-summary :summary-method="getSummaries">
				<el-table-column fixed prop="id" label="ID" align="center">
				</el-table-column>
				<el-table-column fixed>
					<template slot-scope="scope">
						<block v-for="item in scope.row.thumb_url">
							<img :src="item" width="50" height="50" />
						</block>
					</template>
				</el-table-column>
				<el-table-column prop="title" label="配套名字" align="center">
				</el-table-column>
				<el-table-column prop="total" label="FV" align="center">
				</el-table-column>
				<el-table-column prop="productprice" label="价格 MYR" align="center">
				</el-table-column>
				<el-table-column prop="minprice" label="最低价 MYR" align="center">
				</el-table-column>
				<el-table-column prop="maxprice" label="最高价 MYR" align="center">
				</el-table-column>
				<el-table-column prop="totalnum" label="数量" align="center">
				</el-table-column>
				<el-table-column prop="totalprice" label="总计 MYR" align="center">
				</el-table-column>
			</el-table>
			<div class="sumbit">
				<el-button size="small" type="primary" @tap="hanldedetail">提交</el-button>
			</div>
		</el-card>
	</view>
</template>

<script>
	export default {
		name: "product-detail",
		props: {
			todatail: {
				type: Array,
				default: () => []
			},
		},
		data() {
			return {
				inputwith: '65%',
				currentPage1: 5,
				labelPosition: 'right',
				fromleft: '40rpx',
				email: '',
				phone: '',
				tableData: '',
			};
		},
		mounted() {
			this.initialize()
			window.addEventListener('resize', this.handleResize); // 监听窗口大小变化
		},
		beforeDestroy() {
			window.removeEventListener('resize', this.handleResize); // 移除监听事件
		},
		methods: {
			getSummaries(param) {
				// console.log(param)
				const {
					columns,
					data
				} = param;
				const sums = [];
				columns.forEach((column, index) => {
					if (index === 0) {
						sums[index] = '总价';
						return;
					}
					if(index===7||index===8){
						const values = data.map(item => Number(item[column.property]));
						if (!values.every(value => isNaN(value))) {
							sums[index] = values.reduce((prev, curr) => {
								const value = Number(curr);
								if (!isNaN(value)) {
									return prev + curr;
								} else {
									return prev;
								}
							}, 0);
							if(index===8){
								// console.log(sums[index])
								sums[index] = sums[index].toFixed(2)
							}
						}
					}
					
				});
				return sums;
			},
			async initialize() {
				// console.log('接收到的数据',this.todatail)
				let productDetails = this.todatail
				let self = this
				const {
					phone,
					email
				} = productDetails[productDetails.length - 1]
				self.email = email
				self.phone = phone
				let ids = []
				// productDetails.forEach(val=>{
				// 	ids.push(val['id'])
				// })
				let newids = productDetails.map(({
					id,
					count
				}) => ({
					id,
					count
				}));
				newids.pop();
				// console.log(newids)
				var tabledata = []
				for (let i = 0; i < newids.length; i++) {
					await self.$axios.get(
							'/plugin/index.php?i=1&f=guide&m=many_shop&d=mobile&r=uniapp.goods.detail&id=' + newids[i]
							.id + '&num=' + newids[i].count)
						.then(res => {
							const {
								result
							} = res
							// console.log(res)
							tabledata.push(result)
						})
						.catch(err => {
							console.log(err)
						})
				}
				self.tableData = tabledata
				// console.log('添加完成',tabledata)
			},
			getScreenWidth() {
				this.screenWidth = window.innerWidth;
				if (this.screenWidth <= 990) {
					// this.labelw = '220rpx'
					this.labelPosition = 'top'
					this.inputwith = '100%'
					this.fromleft = '0rpx'
				} else {
					this.labelPosition = 'right'
					this.inputwith = '65%'
					this.fromleft = '40rpx'
				}
			},
			handleResize() {
				const newScreenWidth = window.innerWidth;
				if (newScreenWidth !== this.screenWidth) {
					this.screenWidth = newScreenWidth;
					console.log(newScreenWidth);
					if (newScreenWidth <= 990) {
						this.fromleft = '0rpx'
						this.labelPosition = 'top'
						this.inputwith = '100%'
					} else {
						this.fromleft = '40rpx'
						this.labelPosition = 'right'
						this.inputwith = '65%'
					}
				}
			},
		}
	}
</script>

<style>
	.equal-width-column {
		width: 100%;
	}

	.sumbit {
		margin-top: 20rpx;
		display: flex;
		justify-content: center;
	}
</style>